
import { defineComponent } from 'vue';
import { useRouter } from 'vue-router';

// Components
import Tabs from '../components/Tabs.vue';

export default defineComponent({
  name: 'HomeView',

  components: {
    Tabs
  },

  computed: {
    isHomePage: () => {
      return useRouter().currentRoute.value.name == 'home'
    }
  }
});
